<template>
  <header class="bg-navbar shadow sm:flex sm:justify-between">
      <div class="md:mx-4 flex p-3 justify-between items-center">
        <div class="text-default text-xl font-bold ">
          Dashboard
        </div>
        <div class="flex text-default">
          <div class="text-xl text-default p-2 sm:hidden">
            <button class="hover:bg-page focus:outline-none"  @click.prevent="changeMode">
              <div v-if="!darkMode"><i class="bx bx-moon"></i></div>
              <div v-if="darkMode"><i class="bx bx-sun"></i></div>
            </button>
          </div>
          <div class="sm:hidden text-xl p-2">
            <button class="hover:bg-page focus:outline-none"  @click.prevent="isOpen = !isOpen">
              <i class="bx bx-menu"></i>
            </button>
          </div>
        </div>
      </div>
      <div class="hidden md:block w-1/3 p-3">
        <input class="apparience-none rounded-lg bg-page w-full p-2 text-default" placeholder="Buscar.."> 
      </div>
      <div class="md:mx-8 text-default font-semibold sm:flex sm:items-center" :class="isOpen ? 'block' : 'hidden'">
        <button class="hover:bg-page py-3 px-3 hidden sm:block focus:outline-none"  @click.prevent="changeMode">
          <div v-if="!darkMode"><i class="bx bx-moon"></i></div>
          <div v-if="darkMode"><i class="bx bx-sun"></i></div>
        </button>
        <a href="#" class="hover:bg-page block py-3 px-3">Inicio</a>
        <a href="#" class="hover:bg-page block py-3 px-3">Inicio</a>
        <hr>
        <!-- Elemen of sidbar when is a smaller screen  -->
        <a href="#" class="hover:bg-page block py-3 px-3 sm:hidden">Inicio</a>
        <a href="#" class="hover:bg-page block py-3 px-3 sm:hidden">Inicio</a>
        <a href="#" class="hover:bg-page block py-3 px-3 sm:hidden">Inicio</a>
        <a href="#" class="hover:bg-page block py-3 px-3 sm:hidden">Inicio</a>
      </div>
  </header>
</template>

<script>
export default {
  data() {
    return {
      isOpen: false, 
      darkMode: true,
      theme: {0:'theme-dark',1:'theme-light'},
    }
  },
  methods: {
    changeMode(){
      document.body.classList.replace(this.theme[this.darkMode?0:1],this.theme[!this.darkMode?0:1]);
      this.darkMode = !this.darkMode;
    }
  },
};
</script>
